.u-center-text{
  text-align:center !important
}

.u-margin-bottom-small{
  margin-bottom:0.5rem !important
}

.u-margin-bottom-medium{
  margin-bottom:1rem !important
}

.u-margin-top-big{
  margin-top:5rem !important
}

.heading-primary{
  color:#fff;
  text-transform:uppercase;
  -webkit-backface-visibility:hidden;
  backface-visibility:hidden;
  margin-bottom:2rem
}

.heading-primary--main{
  display:block;
  font-size:1.5rem;
  font-weight:400;
  letter-spacing:3.5rem;
  animation-name:moveInLeft;
  animation-duration:1s;
  animation-timing-function:ease-out
}

.heading-primary--sub{
  display:block;
  font-size:1.5rem;
  font-weight:700;
  letter-spacing:1.75rem;
  animation:moveInRight 1s ease-out
}

.heading-secondary{
  font-size:1.5rem;
  text-transform:uppercase;
  font-weight:700;
  color:transparent;
  letter-spacing:0.2rem;
  line-height:1;
  transition:all 0.2s;
  color:#652e8d
}

.btn,.btn:link,.btn:visited{
  text-transform:uppercase;
  text-decoration:none;
  padding:0.5rem 4rem;
  display:inline-block;
  border-radius:0.5rem;
  transition:all 0.2s;
  position:relative;
  font-size:1.2rem;
  border:none;
  cursor:pointer
}

.btn:hover{
  transform:translateY(-3px);
  box-shadow:0 1rem 2rem rgba(0,0,0,0.2)
}

.btn:hover::after{
  transform:scaleX(1.4) scaleY(1.6);
  opacity:0
}

.btn:active,.btn:focus{
  outline:none;
  transform:translateY(-1px);
  box-shadow:0 0.5rem 1rem rgba(0,0,0,0.2)
}

.btn--white{
  background-color:#fff;
  color:#777
}

.btn--white::after{
  background-color:#fff
}

.btn--green{
  background:linear-gradient(to right, #fc466b, #3f5efb);
  color:#fff
}

.btn--green::after{
  background-color:#55c57a
}

.section-plans{
  padding:1rem 0
}

.card{
  perspective:150rem;
  -moz-perspective:150rem;
  position:relative;
  height:10rem
}

@media (max-width: 760px){
  .card{
    perspective:75rem;
    -moz-perspective:75rem;
    height:5rem
  }
}

.card__side{
  height:10rem;
  transition:all 0.8s ease;
  position:absolute;
  top:0;
  left:0;
  width:100%;
  -webkit-backface-visibility:hidden;
  backface-visibility:hidden;
  border-radius:3px;
  overflow:hidden;
  box-shadow:0 0.5rem 4rem rgba(0,0,0,0.15)
}

@media (max-width: 760px){
  .card__side{
    height:5rem
  }
}

.card__side--front{
  background-color:#fff
}

.card__side--front-1{
  background:linear-gradient(-45deg, #f403d1, #64b5f6)
}

.card__side--front-2{
  background:linear-gradient(-45deg, #f321d7, #ffec61)
}

.card__side--front-3{
  background:linear-gradient(-45deg, #24ff72, #9a4eff)
}

.card__side--back{
  transform:rotateY(180deg)
}

.card__side--back-1{
  background:linear-gradient(-45deg, #64b5f6, #f403d1)
}

.card__side--back-2{
  background:linear-gradient(-45deg, #ffec61, #f321d7)
}

.card__side--back-3{
  background:linear-gradient(-45deg, #9a4eff, #24ff72)
}

.card:hover .card__side--front-1,.card:hover .card__side--front-2,.card:hover .card__side--front-3{
  transform:rotateY(-180deg)
}

.card:hover .card__side--back{
  transform:rotateY(0)
}

.card__title{
  height:10rem;
  padding:4rem 2rem 2rem;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center
}

@media (max-width: 760px){
  .card__title{
    height:5rem;
    padding:1rem
  }
}

.card__title--1 .fas{
  font-size:5rem
}

.card__title--2 .fas{
  font-size:5rem
}

.card__title--3 .fas{
  font-size:5rem
}

.card__heading{
  font-size:4rem;
  font-weight:300;
  text-transform:uppercase;
  text-align:center;
  color:#fff;
  width:75%
}

@media (max-width: 760px){
  .card__heading{
    font-size:3rem
  }
}

.card__heading-span{
  padding:1rem 0.5rem;
  -webkit-box-decoration-break:clone;
  box-decoration-break:clone
}

.card__details{
  padding:0 2rem 2rem
}

.card__details ul{
  list-style:none;
  width:80%;
  margin:0 auto
}

.card__details ul li{
  text-align:center;
  font-size:0.5rem;
  padding:1rem
}

.card__details ul li:not(:last-child){
  border-bottom:1px solid #652e8d
}

.card__cta{
  position:absolute;
  top:50%;
  left:50%;
  transform:translate(-50%, -50%);
  width:90%;
  text-align:center
}

@media (max-width: 760px){
  .card__cta{
    display:inline-flex;
    vertical-align:middle
  }

  .card__cta button{
    float:right;
    margin:auto
  }
}

.card__price-box{
  text-align:center;
  color:#fff;
  margin-bottom:1rem
}

@media (max-width: 760px){
  .card__price-box{
    margin-bottom:0
  }
}

.card__price-only{
  font-size:1.4rem;
  text-transform:uppercase
}

@media (max-width: 760px){
  .card__price-only{
    font-size:1rem
  }
}

.card__price-value{
  font-size:1.5rem;
  font-weight:100
}

@media (max-width: 760px){
  .card__price-value{
    font-size:0.8rem
  }
}

.row{
  max-width:114rem;
  margin:0 auto;
  display:flex;
  flex-flow:row wrap;
  justify-content:center
}

.row::after{
  content:"";
  display:table;
  clear:both
}

.row [class^="col-"]{
  float:left
}

.row [class^="col-"]{
  margin:1rem
}

.row .col-1-of-3{
  display:grid;
  grid-template-columns:repeat(auto-fill, minmax(280px, 1fr));
  grid-gap:20px
}

@media (max-width: 1000px){
  .row .col-1-of-3{
    grid-template-columns:repeat(auto-fill, minmax(275px, 1fr))
  }
}

